  let logScrollEvent = function (left, date, direction) {
    return
    console.log(left, 'left')
    console.log(preleft, 'preleft')
    if (preleft !== 0 && date) {
      let second = moment(date.format('YYYY-MM-DD HH:mm:ss')).diff(moment(fromScrollDate), 'second')
      fromScrollDate = moment(fromScrollDate).add(second - 1, 'second').format('YYYY-MM-DD HH:mm:ss')
      toScrollDate = moment(toScrollDate).add(second, 'second').format('YYYY-MM-DD HH:mm:ss')

      if (initVisibleGanttDataTimer) {
        $timeout.cancel(initVisibleGanttDataTimer)
      }
      initVisibleGanttDataTimer = $timeout(function () {
        $scope.initVisibleGanttData()
      }, 300)
    }
    //#region
    // else {
    //   fromScrollDate = moment($scope.options.fromDate).add(- 1, 'second').format('YYYY-MM-DD HH:mm:ss')

    //   const scale = document.getElementById('scaleModeStorage').value
    //   let splittedViewScale
    //   let viewScaleValue = 1
    //   let viewScaleUnit = scale
    //   if (scale) {
    //     splittedViewScale = scale.split(' ')
    //   }
    //   if (splittedViewScale && splittedViewScale.length > 1) {
    //     viewScaleValue = splittedViewScale[0]
    //     viewScaleUnit = splittedViewScale[splittedViewScale.length - 1]
    //   }
    //   if (viewScaleUnit === 'day') {
    //     toScrollDate = moment($scope.options.fromDate).add(viewScaleValue, 'day').format('YYYY-MM-DD HH:mm:ss')
    //   } else {
    //     toScrollDate = moment($scope.options.fromDate).add(viewScaleValue, 'hour').format('YYYY-MM-DD HH:mm:ss')
    //   }

    //   if (initVisibleGanttDataTimer) {
    //     $timeout.cancel(initVisibleGanttDataTimer)
    //   }
    //   initVisibleGanttDataTimer = $timeout(function () {
    //     $scope.initVisibleGanttData()
    //   }, 300)
    // }
    //#endregion
    preleft = left
    // if (date !== undefined) {
    //   $log.info('[Event] api.on.scroll: ' + left + ', ' + (date === undefined ? 'undefined' : date.format()) + ', ' + direction)
    // }
  }

  // Event handler
  let logDataEvent = function (eventName) {
    $log.info('[Event] ' + eventName)
  }

  let curTask
  let preTask
  // Event handler
  let logTaskEvent = function (eventName, task) {
    $log.info('[Event] ' + eventName + ': ' + task.model.name)
    if (eventName === 'task-click') {
      curTaskId = task.model.id
      curTaskFrom = task.model.from
      curRowName = task.row.model.name
      preTask = curTask
      curTask = task
      if (task.model.childTasks && task.model.childTasks.length > 1) {
        curTaskId = document.getElementById('taskIdStorage').value = ''
      }
      const taskColorMode = document.getElementById('taskColorsStorage').value
      const taskHighlight = document.getElementById('taskHighlightStorage').value
      if (taskHighlight !== '') {
        $scope.initTaskColors()
        $scope.initTaskHighlightColors()
      } else {
        if (curTask.model.color === CellSelectedColor) {
          if (taskColorMode === '1') {
            curTask.model.color = curTask.model.moAheadDelayColor
          } else if (taskColorMode === '2') {
            curTask.model.color = curTask.model.moKitColor
          } else {
            curTask.model.color = curTask.model.moStatusColor
          }
        } else {
          curTask.model.color = CellSelectedColor
        }
        if (preTask && preTask !== curTask) {
          if (taskColorMode === '1') {
            preTask.model.color = preTask.model.moAheadDelayColor
          } else if (taskColorMode === '2') {
            preTask.model.color = preTask.model.moKitColor
          } else {
            preTask.model.color = preTask.model.moStatusColor
          }
        }
      }

      $scope.data.forEach(rowData => {
        if (rowData.tasks.length > 0) {
          rowData.tasks.forEach(task => {
            task.dependencies = []
          })
        }
      })
      curTask.model.dependencies = curTask.model.tempDependencies
      $scope.initParentDependencies(curTask.model.id)
      $log.info('initParentDependencies 递归结束')
      $scope.initChildDependencies(curTask.model.dependencies)
      $log.info('initChildDependencies 递归结束')
      $scope.api.dependencies.refresh()
    } else if (eventName === 'tasks.on.moveBegin') {
      $scope.data.forEach(rowData => {
        let curMoResCodePriority = task.model.moResCodePriorities.find(moResCodePriority => moResCodePriority.Key === rowData.name)
        if (curMoResCodePriority) {
          rowData.color = RowSelectedColor
          rowData.priorityTips = curMoResCodePriority.Value
          // $scope.options.timeFramesWorkingMode = 'hidden'
        } else {
          rowData.color = ''
          rowData.priorityTips = ''
        }
      })
    } else if (eventName === 'tasks.on.moveEnd') {
      $scope.data.forEach(rowData => {
        rowData.color = ''
        rowData.priorityTips = ''
        // $scope.options.timeFramesWorkingMode = 'visible'
      })
    }
  }

  // Event handler
  let logRowEvent = function (eventName, row) {
    $log.info('[Event] ' + eventName + ': ' + row.model.name)
    if ((eventName === 'row-click' || eventName === 'row-label-click')) {
      curRowName = row.model.name
      $scope.initGanttResourceCalendars()
      $scope.data.forEach(rowData => {
        if (rowData.name === row.model.name) {
          rowData.color = RowSelectedColor
        } else {
          rowData.color = ''
        }
      })
    }
  }

