  let ganttResourceCalendars
  $scope.initGanttResourceCalendars = function () {
    if (curRowName && ganttResourceCalendars) {
      let curGanttResourceCalendar = ganttResourceCalendars.filter(resCalendar => resCalendar.ResourceCode === curRowName)
      if (curGanttResourceCalendar && curGanttResourceCalendar.length > 0) {
        let timeFrames = {}
        for (let key in curGanttResourceCalendar[0].TimeFrames) {
          timeFrames[key] = {
            start: moment(curGanttResourceCalendar[0].TimeFrames[key].Start, 'HH:mm'),
            end: moment(curGanttResourceCalendar[0].TimeFrames[key].End, 'HH:mm'),
            color: curGanttResourceCalendar[0].TimeFrames[key].Color,
            working: curGanttResourceCalendar[0].TimeFrames[key].Working,
            defualt: curGanttResourceCalendar[0].TimeFrames[key].Defualt
          }
        }

        let dateFrames = {}
        for (let key in curGanttResourceCalendar[0].DateFrames) {
          let year = key.slice(0, 4)
          let month = key.slice(4, 6)
          let day = key.slice(6, 8)
          dateFrames[key] = {
            date: moment(year + '-' + month + '-' + day, 'YYYY-MM-DD'),
            targets: curGanttResourceCalendar[0].DateFrames[key]
          }
        }

        $scope.options.timeFrames = timeFrames
        $scope.options.dateFrames = dateFrames
      }
    }
  }

